.custom-button {
    min-width: 200px;
    border-radius: 30px;
    text-align: center;
    padding: 15px 0px;
    box-shadow: 1px 1px 20px 7px #665dfe4f;
    color: rgba(26, 27, 29, 0.933);
    transition: 0.3s ease-in;
    transition: 0.4s ease;
}

.custom-button:hover {
    background-color: #665dfe;
    color: #edeeee;
}


.sign-up,
.sign-in {
    max-width: 500px;
    margin: auto;
}
.sign-up {
    display: flex;
    flex-direction: column;
}
.sign-up .title {
    margin: 10px 0;
}

.sign-up .buttons {
    display: flex;
    justify-content: space-between;
}

.auth-page-btn-c {
    width: 100%;
    text-align: center;
    padding: 50px 0px;
}
.btn-group {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}
.auth-btn {
    min-width: 150px;
    text-align: center;
    padding: 15px 0px;
    background-color: #665dfe1c;
    box-shadow: 1px 1px 20px 7px #665dfe4f;
    color: rgba(26, 27, 29, 0.933);
    transition: 0.3s ease-in;
}
.auth-btn-l {
    border-radius: 250px 0px 0px 25px;
}
.auth-btn-r {
    border-radius: 0px 25px 250px 0px;
}
.auth-btn-active {
    background-color: #665dfe;
    color: #edeeee;
    box-shadow: -1px 14px 46px 1px #665dfe;
}
.auth-btn-active:hover {
    color: #dadada;
}

.message-input {
    border-radius: 30px;
    width: 90%;
    padding: 10px 20px;
    left: 0;
    border-color: transparent;
    background-color: transparent;
}
.message-input:focus {
    padding: 10px 20px;
    left: 0;
    border-color: transparent;
    outline: none;
    background-color: #ffff;
}

.main-visible {
  justify-content: center;
  align-items: center;
}

.list-username{
    font-size: 12px;
}
.call-pop {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 100;
    background-color: #ffff;
    border: 1px solid #14154e4d;
    border-radius: 5px;
    padding: 15px 15px;
    font-size: 18px;
    line-height: 24px;
    font-family: Montserrat;
}

.btns-c {
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn-c button {
    padding: 10px 15px;
    border-radious: 10px;
    margin: 0px 10px;
    min-width: 110px;
    text-transform: center;
}

.vid-max {
    transition: 1s;
}
.myId {
    margin-right: 5rem;
    border-radius: 5px;
    background: #c9d6ff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
        to right,
        #e2e2e2,
        #c9d6ff
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    padding: 2rem;
    display: grid;
    justify-content: center;
    align-content: center;
}

.call-button {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    text-align: center;
    font-size: 20px;
    color: white;
}

.video-container {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
}
.video-btn-container {
    position: absolute;
    bottom: 0;
    padding: 20px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.video-btn-container-inner {
    /* background-color: #ffffffa6; */
    position: absolute;
    bottom: 0;
    border-radius: 100px;
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}
.btn-center-item {
}

.caller {
    text-align: center;
    color: #fff;
}

.video {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
}

/*  */
#container {
    position: absolute;
    width: 200px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    position: relative;
}

#container .circle {
    border-radius: 50%;

    width: 60px;
    height: 60px;
    position: absolute;
    opacity: 0;
}

#container .item {
    z-index: 100;
    padding: 5px;
}

.circle {
    border-radius: 50%;
    border: 2px solid #00ff42;
    /* background-color: #00ff42; */
    width: 50px;
    height: 50px;
    position: absolute;
    opacity: 0;
    -webkit-animation: scaleIn 2s infinite cubic-bezier(0.36, 0.11, 0.89, 0.32);
            animation: scaleIn 2s infinite cubic-bezier(0.36, 0.11, 0.89, 0.32);
}

.circle-x {
    background-color: #00ff42;
    border-radius: 50%;
    width: 65px;
    height: 65px;
    position: absolute;
    opacity: 1;
    z-index: 10;
}
.circle-br {
    border-radius: 50%;
    width: 65px;
    height: 65px;
    position: absolute;
    opacity: 1;
    background-color: #e11b22;
    z-index: 10;
}
@-webkit-keyframes scaleIn {
    from {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        opacity: 1;
        border: 2px solid #138e33;
    }
    to {
        -webkit-transform: scale(2, 2);
                transform: scale(2, 2);
        opacity: 0;
        border: 0px solid #138e33;
    }
}
@keyframes scaleIn {
    from {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        opacity: 1;
        border: 2px solid #138e33;
    }
    to {
        -webkit-transform: scale(2, 2);
                transform: scale(2, 2);
        opacity: 0;
        border: 0px solid #138e33;
    }
}

.minimize-c {
    position: absolute;
    z-index: 100;
    padding: 5px 20px;
    right: 20px;
    top: 20px;
    background: #ffffff3d;
    color: #000;
    border-radius: 20px;
    cursor: pointer;
}
.info-c {
    position: absolute;
    z-index: 100;
    padding: 5px 20px;
    left: 20px;
    top: 20px;
    border-left: 11px solid #138e33;
    background: rgb(3, 1, 29);
    background: linear-gradient(
        90deg,
        rgba(3, 1, 29, 0.9139005944174545) 0%,
        rgba(255, 255, 255, 0) 100%,
        rgba(252, 70, 107, 0.3200630594034489) 200%
    );
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#03011d",endColorstr="#fc466b",GradientType=1);
    opacity: 0.8;
    transition: 0.1s ease-in-out;
}
.info-c h1 {
    font-family: 'Montserrat', sans-serif;
    font-size: 36px;
    color: #1ac447;
    font-weight: 500;
    /* color: #ffff; */
}
.info-c p {
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 300;
    color: #ffff;
    line-height: 1px;
    margin-bottom: 4px;
}
.info-c:hover {
    opacity: 1;
}

.full-screen-btn {
    height: 14px;
    width: 14px;
    cursor: pointer;
    display: none;
    color: white;
    transition: 0.1s;
}

.vid-min {
    inset: auto !important;
    bottom: 20px !important;
    right: 20px !important;
    padding: 0px !important;
    background-color: transparent !important;
    height: 180px !important;
    width: 320px !important;
}
.vid-min .info-c {
    display: none;
}
.vid-min .minimize-c {
    display: none;
}
.vid-min .video-btn-container-inner {
    display: none;
}

.vid-min .full-screen-btn {
    display: block;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

